import "./css/PwdEmail.css";
import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import Modal from "react-bootstrap/Modal";
import Form from "react-bootstrap/Form";
import axiosApi from "../Util/api";

const PwdEmail = (props) => {
  const [gotId, setGotId] = useState();
  const [gotName, setGotName] = useState();
  const [gotEmail, setGotEmail] = useState();

  const handleClose = () => {
    props.pwdEmailShowFunc(false);
  };

  return (
    <>
      <Modal show={props.pwdEmailShow} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>  </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form>
            <Form.Group className="mb-3" controlId="id">
              <Form.Label>ID()</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter ID"
                onChange={(e) => setGotId(e.target.value)}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="name">
              <Form.Label>Name()</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter Name"
                onChange={(e) => setGotName(e.target.value)}
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="email">
              <Form.Label>Email()</Form.Label>
              <Form.Control
                type="email"
                placeholder="Enter Email"
                onChange={(e) => setGotEmail(e.target.value)}
              />
            </Form.Group>
          </Form>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
            
          </Button>
          <Button
            variant="primary"
            onClick={() => {
              axiosApi
                .patch("/api/seller/checkpwdmail", {
                  sellerId: gotId,
                  name: gotName,
                  email: gotEmail,
                })
                .then((res) => {
                  if (
                    res.data.indexOf("   .") <
                    0
                  ) {
                    handleClose();
                    alert(res.data);
                  } else {
                    if (window.confirm(res.data)) {
                      axiosApi
                        .patch("/api/seller/pwdmail", {
                          sellerId: gotId,
                          email: gotEmail,
                        })
                        .then((res) => {
                          handleClose();
                          alert(res.data);
                        })
                        .catch((err) => {
                          console.log(err);
                        });
                    }
                  }
                })
                .catch((err) => {
                  alert(err.response.data.message);
                });
            }}
          >
             
          </Button>
        </Modal.Footer>
      </Modal>
    </>
  );
};

export default PwdEmail;
